@import '../../../variable.scss';

.pagination {
    width: 100%;
    height: 60px;
   
    &__container {
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    &__wrapper {
      display: flex;
      width: min-content;
      justify-content: center;
      align-items: center;
      span {
        color: $buttonColor;
      }
    }
    &__button {
      min-width: 20px;
      min-height: 20px;
      cursor: pointer;
      background-color: transparent;
      text-align: center;
      span {
        display: inline-block;
        position: relative;
        text-align: center;
        z-index: 10;
        &::before {
          position: absolute;
          content: "";          
          margin-left: -10px;
          background-color: transparent;
          top: -1px;
          left: 50%;
          min-width: 20px;
          min-height: 20px;
          z-index: -1;
        }
      }
      &.active span {
        color: #fff;
      }
      &.active span::before {
        background-color: $buttonColor;
        border-radius: 50%;
      }
      &.none {
        display: none;
      }
    }
  }